@import url('https://fonts.googleapis.com/css?family=Nunito:300,400,600,700,800,900');

html,
body{
  font: normal 400 16px/150% 'Nunito', sans-serif;
  color: $black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  @media screen and (min-width: 2400px){
    font: normal 400 18px/150% 'Nunito', sans-serif;
  }
}

h1, .h1,
h1 a, .h1 a{
  margin-bottom: 2rem;
  font-family: 'Nunito', sans-serif;
  font-size: 2rem;
  font-weight: 400;
  color: $primary;
}

h2, .h2,
h2 a, .h2 a{
  margin-bottom: 1.5rem;
  padding-bottom: 0.25rem;
  border-bottom: 2px dashed rgba($secondary, 0.25);
  font-family: 'Nunito', sans-serif;
  line-height: 150%;
  font-size: 1.25rem;
  font-weight: 700;
  text-transform: uppercase;
  color: $secondary;
}

h3, .h3,
h3 a, .h3 a{
  margin-bottom: 0;
  font-family: 'Nunito', sans-serif;
  line-height: 150%;
  font-size: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  color: $black;
}

p{
  &.lead{
    font-family: 'Nunito';
    font-weight: 400;
    line-height: 2rem;
    color: $black;
  }

  a{
    text-decoration: underline;
  }
}

small{
  font-size: 0.9rem;
  font-weight: 500;
  color: $black;
}

a{
  color: $primary;
  transition: color 0.5s ease;
  text-decoration: underline;

  &:hover{
    color: $secondary;
  }
}

ul{
  margin: 0 0 1rem 1.2rem;
  padding: 0;
  list-style-type: circle;

  &.list-unstyled{
    margin: 0;
    padding: 0;

    li{
      margin: 0.5rem 0;
    }
  }

  .list-inline-item:not(:last-child){
    margin-right: 1.5rem;
  }
}
