.btn {
  padding: .25rem 1rem;
  text-decoration: none; }

.btn-primary:not(:disabled):not(.disabled) {
  background: #725EBF;
  border: 2px solid #725EBF;
  color: #fff;
  font-weight: 600; }
  .btn-primary:not(:disabled):not(.disabled):hover, .btn-primary:not(:disabled):not(.disabled).active {
    background: #5fb8d3;
    border-color: #5fb8d3; }

.btn-secondary:not(:disabled):not(.disabled) {
  background: transparent;
  border: 2px solid #725EBF;
  color: #725EBF;
  font-weight: 600; }
  .btn-secondary:not(:disabled):not(.disabled):hover, .btn-secondary:not(:disabled):not(.disabled).active {
    background: transparent;
    border-color: #5fb8d3;
    color: #5fb8d3; }

.btn-tertiary {
  background: transparent;
  border: 2px solid #5fb8d3;
  color: #5fb8d3;
  font-weight: 600; }
  .btn-tertiary:hover {
    background: #5fb8d3;
    border-color: #5fb8d3;
    color: #fff; }

.btn-white {
  background: transparent;
  border: 2px solid #fff;
  color: #fff;
  font-weight: 600; }
  .btn-white:hover {
    background: #fff;
    border-color: #fff;
    color: #725EBF; }

.badge {
  padding: 4px 0.5rem 2px;
  border: 2px solid #fff;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.15); }

.badge-primary {
  background: #725EBF; }

.input-group .input-group-text {
  background: #fff; }

.fa,
.fas {
  color: #4b646b; }

/**
* Gradients
*/
.bg-gradient, .progress-bar {
  background: -webkit-linear-gradient(135deg, rgba(155, 211, 228, 0.5), rgba(194, 185, 227, 0.5), white);
  background: linear-gradient(-45deg, rgba(155, 211, 228, 0.5), rgba(194, 185, 227, 0.5), white);
  background-size: 400%;
  -webkit-animation: Gradient 10s ease infinite;
  animation: Gradient 10s ease infinite;
  box-shadow: inset 0 0 30px rgba(0, 0, 0, 0.1); }

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

/**
* Colors
*/
.bg-gray {
  background: #fcfcfc; }

.progress-bar {
  position: relative;
  width: 100%;
  height: 12px;
  border-radius: 6px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2); }
  .progress-bar .progress {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    border-radius: 6px;
    background: #725EBF;
    opacity: 0.5;
    box-shadow: inset 1px 1px 4px rgba(255, 255, 255, 0.75); }

