.table.table-borderless th,
.table.table-borderless td {
  line-height: 100%;
  padding: 15px 0; }

.carousel {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  white-space: nowrap; }
  .carousel .slider-wrapper {
    position: relative;
    width: 100%;
    height: calc(100% - 70px);
    -webkit-transition: -webkit-transform ease-out 0.45s;
    transition: -webkit-transform ease-out 0.45s;
    transition: transform ease-out 0.45s;
    transition: transform ease-out 0.45s, -webkit-transform ease-out 0.45s; }
    .carousel .slider-wrapper .slide {
      display: inline-block;
      height: 100%;
      width: 100%; }
      .carousel .slider-wrapper .slide img {
        width: 100%;
        height: 100%;
        object-fit: cover; }
  .carousel .carousel-arrows {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 70px;
    background: #fff; }
    .carousel .carousel-arrows .back-arrow,
    .carousel .carousel-arrows .next-arrow {
      position: absolute;
      margin: calc(1rem - 2px) 1rem;
      padding: 8px 10px;
      border: 1px dashed rgba(114, 94, 191, 0.5);
      border-radius: 100%;
      width: 40px;
      height: 40px;
      font-size: 1.25rem;
      cursor: pointer;
      -webkit-transition: all 0.25s ease;
      transition: all 0.25s ease; }
      .carousel .carousel-arrows .back-arrow .fa,
      .carousel .carousel-arrows .next-arrow .fa {
        color: #725EBF; }
      .carousel .carousel-arrows .back-arrow:hover,
      .carousel .carousel-arrows .next-arrow:hover {
        background: #5fb8d3;
        color: #fff;
        border-color: #5fb8d3; }
    .carousel .carousel-arrows .back-arrow {
      left: 0; }
    .carousel .carousel-arrows .next-arrow {
      right: 0; }

.header {
  display: flex;
  align-items: center;
  position: fixed;
  z-index: 9;
  width: 100%;
  height: 70px;
  top: 0;
  padding: 1rem 0;
  background: #fff;
  border-top: 1px solid rgba(114, 94, 191, 0.1); }
  .header .logo,
  .header .logo a {
    font-size: 1.5rem;
    letter-spacing: 2px;
    color: #725EBF;
    text-decoration: none; }
  .header .btn {
    padding: 0.1rem 0.5rem; }
  .header .nav-link {
    text-decoration: none;
    color: #555; }
    .header .nav-link:hover {
      text-decoration: none; }

.hero {
  position: relative;
  top: 70px;
  left: 0;
  width: 100%;
  height: calc(100vh - 70px);
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center; }
  @media screen and (min-width: 768px) {
    .hero {
      position: fixed;
      left: 0;
      width: 30%; } }
  .hero h1 {
    font-size: 1.5rem; }
    .hero h1 span {
      display: block;
      margin: 0 0 1rem;
      font-size: 1rem;
      font-weight: 800;
      text-transform: uppercase;
      opacity: 0.75; }

.content {
  position: relative;
  top: 70px;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #fcfcfc;
  overflow: auto; }
  @media screen and (min-width: 768px) {
    .content {
      position: fixed;
      left: 30%;
      width: 70%;
      height: calc(100vh - 70px);
      border-left: 10px solid #fff; } }

